.form {
  display: flex;
  flex-direction: column;

  @media (max-width: $tablet-width) {
    width: 100%;
    padding-top: 70px;
    align-items: center;
    background-color: $white;
  }
}

.form__title {
  margin: 0;
  margin-bottom: 19px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  color: $white;
  letter-spacing: -0.005em;

  @media (max-width: $tablet-width) {
    width: 321px;
    margin-bottom: 30px;
    font-size: 18px;
    color: $black;
  }

  @media (max-width: $mobile-width) {
    width: 288px;
    margin-bottom: 20px;
  }
}

.form input {
  height: 48px;
  margin-bottom: 20px;
  padding-left: 30px;
  font-size: 15px;
  line-height: 24px;
  color: $form-input-text;
  border: 1px solid #c9ccd4;
  box-sizing: border-box;
  border-radius: 6px;
  border: 1px solid $form-border;

  @media (max-width: $tablet-width) {
    width: 321px;
    padding-left: 20px;
  }

  @media (max-width: $mobile-width) {
    width: 288px;
    padding-left: 25px;
  }
}

.form input:focus {
  outline-color: $accent;
}

.form input::placeholder {
  color: rgba($form-input-text, 0.5);
}


.form__button-submit {
  height: 51px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: $black;
  background-color: $accent;
  border-radius: 6px;
  border: none;
  outline: none;
  cursor: pointer;

  &:hover,
  &:focus {
    filter: brightness(120%);
  }

  @media (max-width: $tablet-width) {
    width: 321px;
  }

  @media (max-width: $mobile-width) {
    width: 288px;
  }
}
