.location__title {
  margin: 0;
  margin-bottom: 28px;
  text-align: center;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  color: $title-color;
  letter-spacing: -0.005em;

  @media (max-width: $tablet-width) {
    margin-bottom: 20px;
    font-size: 24px;
  }

  @media (max-width: $mobile-width) {
    font-size: 18px;
  }
}

.location__address {
  margin: 0;
  margin: 0 auto;
  margin-bottom: 41px;
  width: 555px;
  font-size: 17px;
  line-height: 31px;
  text-align: center;
  color: rgba($black, 0.7);

  @media (max-width: $tablet-width) {
    width: auto;
    margin-bottom: 20px;
  }

  @media (max-width: $mobile-width) {
    width: 250px;
    margin-bottom: 19px;
  }
}

.location__map {
  width: 957px;
  margin-bottom: 38px;
  height: 530px;
  background-color: $dark-background;
  background-image: url('../../img/map-desktop.png');
  background-image: -webkit-image-set(url('../../img/map-desktop.webp') 1x);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @media (min-resolution: 2dppx) {
    background-image: url('../../img/map-desktop@2x.png');
    background-image: -webkit-image-set(
      url('../../img/map-desktop@2x.webp') 2x
    );
  }

  @media (max-width: $tablet-width) {
    width: 698px;
    height: 545px;
    margin-bottom: 20px;
    background-image: url('../../img/map-tablet.png');
    background-image: -webkit-image-set(url('../../img/map-tablet.webp') 1x);

    @media (min-resolution: 2dppx) {
      background-image: url('../../img/map-tablet@2x.png');
      background-image: -webkit-image-set(
        url('../../img/map-tablet@2x.webp') 2x
      );
    }
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    height: 250px;
    margin-bottom: 31px;
    background-image: url('../../img/map-mobile.png');
    background-image: -webkit-image-set(url('../../img/map-mobile.webp') 1x);

    @media (min-resolution: 2dppx) {
      background-image: url('../../img/map-mobile@2x.png');
      background-image: -webkit-image-set(
        url('../../img/map-tablet@2x.webp') 2x
      );
    }
  }
}

.location__map-iframe {
  width: 100%;
  height: 100%;
  border: none;
}

.no-js .location__map-iframe {
  display: none;
}
